import styles from "./app-header.module.css";
import { BurgerIcon, ListIcon, Logo, ProfileIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, NavLink } from "react-router-dom";

export const AppHeader = () => {
    const activeStyle:{color:string} = {
        color: "#F2F2F3",
    }

    return (
        <header className={`text_type_main-default ${styles.header}`}>
            <nav className={`${styles.main_nav} pt-4 pb-4`}>

                <ul className={styles.list}>
                    <NavLink to="/" className={`${styles.list_item} ${styles.link} mr-10`} style={({isActive})=> isActive ? activeStyle : {}} >
                        <BurgerIcon type="secondary" />
                        <p className={`text ${styles.link_text} ${styles.active} pl-2`}></p>
                    </NavLink>

                    <NavLink to="feed" className={`${styles.list_item} ${styles.link}`} style={({isActive})=> isActive ? activeStyle : {}}>
                        <ListIcon type="secondary" />
                        <p className={`text ${styles.link_text} pl-2`}> </p>
                    </NavLink>
                </ul>

                <Link to="/" className={`${styles.link} ${styles.logo}`}>
                    <Logo />
                </Link>

                <NavLink to="profile" className={`${styles.list_item} ${styles.link}`} style={({isActive})=> isActive ? activeStyle : {}}>
                    <ProfileIcon type="secondary" />
                    <p className={`text ${styles.link_text} pl-2`}> </p>
                </NavLink>

            </nav>

        </header>
    )
}